import React, { Component } from "react";
import addUser from "../Images/addUser.svg";
import HakAkses from "../Images/hakAkses.svg";
import { Link } from "react-router-dom";
import Skeleton from "react-loading-skeleton";

class ManageUser extends Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,
    };
    this.test = this.test.bind(this);
  }

  test() {
    this.setState({
      show: true,
    });
  }

  componentDidMount() {}
  render() {
    setTimeout(this.test, 1000);
    return (
      <div className="container-fluid" style={{ width: "100%" }}>
        <div className="col-lg-12">
          <div className="row">
            <div className="col-lg-2"></div>
            <div className="col-lg-4 col-md-4">
              {this.state.show === true ? (
                <div className="card-menu">
                  <div className="card-body text-center">
                    <img
                      src={addUser}
                      width="100%"
                      height="50%"
                      alt="Kode Barang"
                    />
                    <h4>Kelola User</h4>
                    <Link to="/ayugold.com/kelolaUser">
                      <button className="btn btn-primary">Masuk</button>
                    </Link>
                  </div>
                </div>
              ) : (
                <Skeleton height={300} width={450} />
              )}
            </div>
            <div className="col-lg-4 col-md-4">
              {this.state.show === true ? (
                <div className="card-menu">
                  <div className="card-body text-center">
                    <img src={HakAkses} width="100%" alt="Kode Barang" />
                    <h4>Kelola Hak Akses</h4>
                    <Link to="/ayugold.com/kelolaHakAkses">
                      <button className="btn btn-primary">Masuk</button>
                    </Link>
                  </div>
                </div>
              ) : (
                <Skeleton height={300} width={450} />
              )}
            </div>
            <div className="col-lg-2"></div>
          </div>
        </div>
      </div>
    );
  }
}

export default ManageUser;
